html, body{
    margin: 0;
    padding: 0;
}

canvas{
    position: absolute;
    top: 0;
    left: 0;
}

.overlay{
    position: relative;
    height: 20px;
    width: 100%;
    top: 50px;
    z-index: 99;
    margin: 0 auto;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 600;
}

.switch{
    position: absolute;
    right: 20px;
}

.overlay img{
    position: absolute;
    left: 50%;
    top: 50px;
    transform: translateX(-50%);
    width: 100%;
    max-width: 310px;
    min-width: 150px;
}

@media (max-width: 600px) {
.overlay img{
    top: 10px;
    max-width: 200px;
}
}